const technologies = () => {
    let technologies = [
        {
            "name": "HTML5",
            "class": "fa-html5"
        },
        {
            "name": "CSS3",
            "class": "fa-css3-alt"
        },
        {
            "name": "JavaScript",
            "class": "fa-js-square"
        },
        {
            "name": "SASS / SCSS",
            "class": "fa-sass"
        },
        {
            "name": "Git",
            "class": "fa-git-alt"
        },
        {
            "name": "GitHub",
            "class": "fa-github"
        },
        {
            "name": "Gulp",
            "class": "fa-gulp"
        }
    ]
    for (i in technologies) {
        let name =  technologies[i].name; 
        let className =  technologies[i].class; 

        //Name
        const nameElement = document.createElement('p');
        nameElement.classList.add('technologies__name');
        nameElement.innerHTML = name;

        // Icon
        const icon = document.createElement('i');
        icon.classList.add('fab', '' + className);

        // Container
        const container = document.createElement('div');
        container.classList.add('technologies_icon');
        container.appendChild(nameElement);
        container.appendChild(icon);

        document.getElementById('technologies').appendChild(container);
    }

}
technologies();
